@mixin transition($property...) {
  transition-property: $property;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
//           @include transition(     , :background-color);

@mixin font($size, $lh, $ls) {
  font-size: $size;
  line-height: $lh;
  letter-spacing: $ls;
}
